	import uniRate from "@/components/uni-rate/uni-rate.vue"  //星星评分

	export default {
		
		components: {
			uniNumberBox,
			uniRate
		},
		data() {
			return {
				//控制渐变标题栏的参数
				beforeHeaderzIndex: 11, //层级
				afterHeaderzIndex: 10, //层级
				beforeHeaderOpacity: 1, //不透明度
				afterHeaderOpacity: 0, //不透明度
				id:"",  //商品id,
				proNum:1, //加入购物车数量
				//是否显示返回按钮



                evaImg:[], //评价上传图片
                tuiList:[], //推荐商品
                guiList:[], //规格
				//轮播主图数据
				swiperList: [],
				//轮播图下标
				currentSwiper: 0,
				anchorlist: [], //导航条锚点
				selectAnchor: 0, //选中锚点
				serviceClass: '', //服务弹窗css类，控制开关动画
				specClass: '', //规格弹窗css类，控制开关动画
				shareClass: '', //分享弹窗css类，控制开关动画
				// 商品信息
			    goodsData:{},
				selectSpec: null, //选中规格
				isKeep: false, //收藏
				// 商品详情信息
				detail:'',
				goodsDetail:{}, //商品详情
			   comment:[], //评论信息
			   // 商品明细
			   goods_list:[],
			   // 规格合集
			   size_list:[],
			   // 选中规格
			   selected_size:'',
			   
			};
		},
		onLoad(option) {

			//小程序隐藏返回按钮
			this.showBack = false;

			//option为object类型，会序列化上个页面传递的参数
			console.log(option); //打印出上个页面传递的参数。
			this.id=option.id;
			this.goodsDetail=option;
		},
		onReady() {
			this.calcAnchor(); //计算锚点高度，页面数据是ajax加载时，请把此行放在数据渲染完成事件中执行以保证高度计算正确
		},
		onPageScroll(e) {
			//锚点切换
			this.selectAnchor = e.scrollTop >= this.anchorlist[2].top ? 2 : e.scrollTop >= this.anchorlist[1].top ? 1 : 0;
			//导航栏渐变
			let tmpY = 375;
			e.scrollTop = e.scrollTop > tmpY ? 375 : e.scrollTop;
			this.afterHeaderOpacity = e.scrollTop * (1 / tmpY);
			this.beforeHeaderOpacity = 1 - this.afterHeaderOpacity;
			//切换层级
			this.beforeHeaderzIndex = e.scrollTop > 0 ? 10 : 11;
			this.afterHeaderzIndex = e.scrollTop > 0 ? 11 : 10;
		},
		//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
		onReachBottom() {
			uni.showToast({
				title: '触发上拉加载'
			});
		},
		mounted() {
            uni.request({
            	url: this.config.url+"goods/detail",
            	data: {
                       token:this.token,
					   id:this.id
            		
            	},
            	method: "post",
            	success: (res) => {
                     console.log("res.data",res.data.data.goods_list);
            		this.goodsData =res.data.data;
            		this.comment=this.goodsData.comment;
					//this.evaImg=this.goodsData.commit[0].comment_covers;
					this.isKeep=this.goodsData.is_collect;
					this.guiList=this.goodsData.specs;
					// 轮播图
					this.swiperList=res.data.data.image.split("|");
					console.log(this.swiperList)
					// 商品详情
					this.detail=res.data.data.content;
					console.log(this.detail);
					// 商品明细
					this.goods_list=res.data.data.goods_list;
					
					// for(var i=0; i<guiList.length;i++){
					// 	this.colorList=guiList[i];
					// 	this.sizeList=guiList[i]
					// }
            	}
            
            });
			  // 推荐商品
			  uni.request({
				url: this.config.url+"recommend",
				data: {
					"token": "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..B32WfMWQ-0QJ1VtEbhxXgtT-nBqc8GwJb3ANBhy8BxU"
			        ,
					
					
				},
				method: "post",
				success: (res) => {
			        // console.log('res1',res);
					 this.tuiList=res.data.data.data
					// this.goodsData =res.data.data;
					// this.comment=this.goodsData.comment;
					// this.evaImg=this.goodsData.commit[0].comment_covers;
					// this.tuiList=this.goodsData.goods_list;
					
				}
			
			})
		},
		methods: {
			// 购买数量
			bindChange(val){
				this.proNum=val;
			},
			addTitle(index){
				console.log(index)
			},
			addColor(size,arr,color,index){
				if(arr.length>0){
					arr.forEach(item=>{
						item.checked=false;
					})
					color.checked=true;
					this.$forceUpdate();
					let check=size.name+":"+color.name;
					// 单个规格项的规格值
					size.checked=check;
					// 所有规格项的规格值
					let all_check='';
					this.guiList.forEach(item=>{
						all_check+=";"+item.checked;
					})
					all_check=all_check.substr(1);
					
					console.log('all_check',all_check);
					// 规格项跟商品对比goos_list显示选中
					this.goods_list.forEach(item=>{
						if(item.goods_spec==all_check){
							this.goodsData.price=item.price_selling;
							this.goodsData.number_stock=item.number_stock;
							// 选中规格项
							this.selected_size=item.goods_spec;
						}
					})
				}
				
				
			},
			//加入购物车
			cancel(){
				// 判断加入购物车
				if(this.selected_size && this.selected_size.indexOf('undefined')==-1){
					uni.request({
						url:this.config.url+"goods/car",
						method:"post",
						data:{
							token:this.token,
							goods_id:this.id,
							goods_spec:this.selected_size,
							number:this.proNum
						},
						success:function(data){
							console.log("data",data);
							if(data.data.code==1){
								uni.showToast({
									title: '加入成功',
									icon:"none"
								});
							}
						}
					})
				}else{
					uni.showToast({
						title: '请选中完整规格',
						icon:"none"
					});
				}
			},
		//	跳转推荐详情
		 gotui(index){
			 uni.navigateTo({
			 	url:"/pages/goods/goods?id="+index
			 })
		 },
			//跳转购物车
			skipCart(){
				uni.switchTab({
					url:"/pages/tabBar/cart"
				})
			},
			//轮播图指示器
			swiperChange(event) {
				this.currentSwiper = event.detail.current;
			},
			//消息列表
			toMsg() {
				uni.navigateTo({
					url: '../msg/msg'
				})
			},
			// 客服
			toChat() {
				uni.navigateTo({
					url: "../msg/chat/chat?name=客服008"
				})
			},
			// 分享
			share() {
				this.shareClass = 'show';
			},
			hideShare() {
				this.shareClass = 'hide';
				setTimeout(() => {
					this.shareClass = 'none';
				}, 150);
			},
			//收藏
			keep() {
				this.isKeep = this.isKeep ? false : true;
				uni.request({
					url:this.config.url+"goods/collect",
					data:{
						token:this.token,
						goods_id:this.id,
					},
					method:"post",
					success:function(res){
						console.log(res)
					}
				})
			},
			// 加入购物车
			joinCart() {
				if (this.selectSpec == null) {
					return this.showSpec(() => {
						uni.showToast({
							title: "已加入购物车"
						});
					});
				}
				uni.showToast({
					title: "已加入购物车"
				});
			},
			//立即购买
			buy() {
				if(this.selected_size && this.selected_size.indexOf('undefined')==-1){
				var goods=JSON.stringify(this.goodsDetail)
					//跳转到结算面
					uni.navigateTo({
						url:"/pages/order/confirmation?id="+this.goodsData.id+'&goodsDetail='+goods+'&price='+this.goodsData.price+'&size='+this.selected_size+'&num='+this.proNum+"&iscart=0"
						
					})
					
				}else{
					uni.showToast({
						title:"请选择完整规格",
						icon:'none',
					})
				}
				// 模板
				/* if (this.selectSpec == null) {
					return this.showSpec(() => {
						this.toConfirmation();
					});
				}
				 this.toConfirmation();*/
			},
			//跳转确认订单页面（模板）
			toConfirmation() {
				let tmpList = [];
				let goods = {
					id: this.goodsData.id,
					img: '../../static/img/goods/p1.jpg',
					name: this.goodsData.name,
					spec: '规格:' + this.goodsData.spec[this.selectSpec],
					price: this.goodsData.price,
					number: this.goodsData.number
				};
				tmpList.push(goods);
				uni.setStorage({
					key: 'buylist',
					data: tmpList,
					success: () => {
						uni.navigateTo({
							url: '../order/confirmation'
						})
					}
				})
			},
			//商品评论
			toRatings() {
				uni.navigateTo({
					url: 'ratings/ratings'
				})
			},
			//跳转评论列表
			showComments(goodsid) {
			
			},
			//选择规格
			setSelectSpec(index) {
				this.selectSpec = index;
			},
			//减少数量
			sub() {
				if (this.goodsData.number <= 1) {
					return;
				}
				this.goodsData.number--;
			},
			//增加数量
			add() {
				this.goodsData.number++;
			},
			//跳转锚点
			toAnchor(index) {
				this.selectAnchor = index;
				uni.pageScrollTo({
					scrollTop: this.anchorlist[index].top,
					duration: 200
				});
			},
			//计算锚点高度
			calcAnchor() {
				this.anchorlist = [{
						name: '主图',
						top: 0
					},
					{
						name: '评价',
						top: 0
					},
					{
						name: '详情',
						top: 0
					}
				]
				let commentsView = uni.createSelectorQuery().select("#comments");
				commentsView.boundingClientRect((data) => {
					let statusbarHeight = 0;
					//APP内还要计算状态栏高度



					let headerHeight = uni.upx2px(100);
					this.anchorlist[1].top = data.top - headerHeight - statusbarHeight;
					this.anchorlist[2].top = data.bottom - headerHeight - statusbarHeight;

				}).exec();
			},
			//返回上一页
			back() {
				uni.navigateBack(1);
			},
			//服务弹窗
			showService() {
				console.log('show');
				this.serviceClass = 'show';
			},
			//关闭服务弹窗
			hideService() {
				this.serviceClass = 'hide';
				setTimeout(() => {
					this.serviceClass = 'none';
				}, 200);
			},
			//规格弹窗
			showSpec(fun) {
				console.log('show');
				this.specClass = 'show';
				this.specCallback = fun;
			},
			specCallback() {
				return;
			},
			//关闭规格弹窗
			hideSpec() {
				this.specClass = 'hide';
				//回调

				this.selectSpec && this.specCallback && this.specCallback();
				this.specCallback = false;
				setTimeout(() => {
					this.specClass = 'none';
				}, 200);
			},
			discard() {
				//丢弃
			}
		}
	};
